"use client";

import { useParams } from "next/navigation";
import Image from "next/image";
import { mockProducts } from "@/app/data/mockProducts";

export default function ProductDetailPage() {
  const { productId } = useParams(); // ✅ Extract params properly
  const product = mockProducts.find((p) => p.id === productId);

  if (!product) {
    return <div className="container mx-auto px-4 py-8">Product not found.</div>;
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="flex flex-col md:flex-row gap-6">
        {/* ✅ Fix Image - add width and height */}
        <Image
          src={product.image}
          alt={product.name}
          width={400}
          height={400}
          className="w-full md:w-1/2 h-auto rounded-lg"
        />
        <div>
          <h1 className="text-3xl font-bold">{product.name}</h1>
          <p className="text-gray-600 text-lg mt-2">${product.price}</p>
          <p className="mt-4">{product.description}</p>
          <button className="mt-6 bg-primary text-white px-6 py-3 rounded-lg">
            Add to Cart
          </button>
        </div>
      </div>
    </div>
  );
}
